@import "./helper.scss";
* {margin: 0;padding: 0;}
* {box-sizing: border-box;}
*::before {box-sizing: border-box;}
*::after {box-sizing: border-box;}
ul, ol {
  list-style: none;
}
a {text-decoration: none; color: inherit;}
body {
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
}
button,input{
  font-family: inherit;
}
:focus{
  outline: none;
}
.clearfix::after {
  content: '';
  display: block;
  clear: both;
}
.icon {
  width: 1em;
  height: 1em;
  display: inline-block;
}

$color-highlight:#f9db6a;
$color-shadow:#f3f3f3;
$color-deep:#1c191c;